.page {
  height: 100%;
  font-family: $text-font;
  color: $basic_black;
  background-color: $basic_white;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.page__wrapper {
  flex: 1 0 auto;
}

.page__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  column-gap: 200px;
  align-items: center;
  padding-top: 34px;
  position: relative;

  @media (max-width: $desktop-width) {
    padding-top: 17px;
    column-gap: unset;

    @media (max-width: $tablet-width) {
      padding-top: 0;
    }
  }
}

.page__logo {
  cursor: pointer;

  &:hover {
    opacity: 0.5;
  }
}

.page__logo svg {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

